.miniheader {
    /* background-color: #fff;
    box-shadow: 1px 1px 4px 0 rgba(0,0,0,.1);
    position: fixed;
    width: 100%; */
    z-index: 3;
}

.miniheader ul {
    margin: 0;
    padding: 0;
    list-style: none;
    overflow: hidden;
}

.miniheader li a {
    display: block;
    padding: 20px 20px;
    text-decoration: none;
}


/* .miniheader li a:hover,
.miniheader .menu-btn:hover {
    background-color: #f4f4f4;
} */


/* menu */

.miniheader .menu {
    clear: both;
    max-height: 0;
    transition: max-height .2s ease-out;
}


/* menu icon */

.miniheader .menu-icon {
    cursor: pointer;
    display: inline-block;
    float: right;
    padding: 28px 20px;
    position: relative;
    user-select: none;
    right: 50px;
}

.miniheader .menu-icon .navicon {
    background: #fff;
    display: block;
    height: 2px;
    position: relative;
    transition: background .2s ease-out;
    width: 18px;
}

.miniheader .menu-icon .navicon:before,
.miniheader .menu-icon .navicon:after {
    background: #fff;
    content: '';
    display: block;
    height: 100%;
    position: absolute;
    transition: all .2s ease-out;
    width: 100%;
}

.miniheader .menu-icon .navicon:before {
    top: 5px;
}

.miniheader .menu-icon .navicon:after {
    top: -5px;
}


/* menu btn */

.miniheader .menu-btn {
    display: none;
}

.miniheader .menu-btn:checked~.menu {
    max-height: 240px;
}

.miniheader .menu-btn:checked~.menu-icon .navicon {
    background: transparent;
}

.miniheader .menu-btn:checked~.menu-icon .navicon:before {
    transform: rotate(-45deg);
}

.miniheader .menu-btn:checked~.menu-icon .navicon:after {
    transform: rotate(45deg);
}

.miniheader .menu-btn:checked~.menu-icon:not(.steps) .navicon:before,
.miniheader .menu-btn:checked~.menu-icon:not(.steps) .navicon:after {
    top: 0;
}


/* 48em = 768px */

@media (min-width: 48em) {
    .miniheader li {
        float: left;
    }
    .miniheader li a {
        padding: 0px 15px;
        font-size: 18px;
        border: none;
    }
    .miniheader .menu {
        clear: none;
        float: right;
        max-height: none;
        padding-right: 30px;
    }
    .miniheader .menu-icon {
        display: none;
    }
}

